import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'

import { AppContext } from '@src-components/contexts'
import { SEO, Layout } from '@src-components/foundations'
import { ArticleContent } from '@src-components/parts'

import { AllSiteMetadataQuery } from '@src-queries'

const Page = props => {
	const { location, data } = props
	const { body } = data.mdx

	const siteMetadata = AllSiteMetadataQuery()

	const [isOpenMenu, setIsOpenMenu] = useState(false)
	const [isOpenSearch, setIsOpenSearch] = useState(false)
	const [isDarkMode, setIsDarkMode] = useState(null)

	const pageDescription = siteMetadata.description

	const pageValues = {
		title: null,
		description: pageDescription,
		location: location.pathname,
		isOpenMenu: isOpenMenu,
		setIsOpenMenu: setIsOpenMenu,
		isOpenSearch: isOpenSearch,
		setIsOpenSearch: setIsOpenSearch,
		isDarkMode: isDarkMode,
		setIsDarkMode: setIsDarkMode,
	}

	return (
		<AppContext.Provider value={pageValues}>
			<SEO />
			<Layout pattern="simple">
				<div className="flex flex-wrap">
					<div className="w-full md:w-1/2">
						<img
							src="https://dummyimage.com/600x400/000/fff"
							className="h-full"
						/>
					</div>
					<div className="w-full md:w-1/2 md:px-16 py-16">
						<h2 className="text-4xl font-bold">
							並の猿から
							<br />
							強きゴリラへ
						</h2>
						<p className="mt-12">
							WordPress テーマ「Snow Monkey」はカスタマイズで強くなる。
						</p>
						<p className="mt-8 text-sm">
							逆引きゴリラは WordPress テーマ「Snow
							Monkey」のカスタマイズを「独自の機械学習 +
							人力整理」によってまとめているウェブメディアです。
						</p>
						<div className="flex mt-16">
							<div className="mr-4 last:mr-0">
								<h3 className="text-sm">推奨ブラウザ</h3>
								<p className="text-sm font-semibold pt-2">
									Safari / Google Chrome
								</p>
							</div>
							<div className="mr-4 last:mr-0">
								<h3 className="text-sm">未対応</h3>
								<p className="text-sm font-semibold pt-2">Internet Explorer</p>
							</div>
						</div>
					</div>
				</div>
				<ArticleContent>{body}</ArticleContent>
			</Layout>
		</AppContext.Provider>
	)
}

Page.propTypes = {
	location: PropTypes.object,
}

export default Page

export const pageQuery = graphql`
	{
		mdx(fields: { type: { eq: "basic" }, name: { eq: "home" } }) {
			id
			body
			frontmatter {
				title
				description
				thumbnail
			}
			fields {
				slug
				name
				type
			}
		}
	}
`
